import { useState } from "react";

import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink
} from "reactstrap";

import styled from "styled-components";

const RMNavbarDiv = styled.div`
  height: 10vh;
  padding: 0;
  margin: 0;
  background: transparent;
  color: white;
  padding-top: 2vh;
  font-size: 1.5em;
  @media (max-width: 576px) {
    padding: 0;
    margin: 0;
    font-size: 4em;
  }

  @media (min-width: 576px) {
    padding: 0;
    margin: 0;
    font-size: 5em;
  }

  @media (min-width: 767px) {
    padding: 0;
    margin: 0;
    font-size: 1.5em;
  }

  @media (min-width: 1200px) {
    padding: 0;
    margin: 0;
  }

  .fa.fa-navicon {
    color: white;
    font-size: 1.75em;
  }

  a {
    color: white;
  }

  .navWrapper {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    font-family: "Oswald", sans-serif;

    @media (max-width: 767px) {
      left: auto;
      top: auto;

      -webkit-transform: translate(0, 0);
      transform: translate(0, 0);
    }
  }

  .collapse {
  }

  .nav-item {
    padding-left: 2vw;
    text-align: center;
    @media (max-width: 767px) {
      z-index: 9999;
      background: rgba(0, 0, 0, 0.8);
      width: 91vw;
    }
  }
`;

function RMNavbar() {
  const [toggle, setToggle] = useState(true);
  return (
    <RMNavbarDiv>
      <Navbar expand="md" sticky="top" className="justify-content-center">
        <NavbarToggler className="" onClick={() => setToggle(!toggle)}>
          <i className="fa fa-navicon" />
        </NavbarToggler>
        <Collapse isOpen={toggle} navbar>
          <div className="navWrapper">
            <Nav className="ml-auto" navbar>
              <NavbarBrand />
              <NavItem>
                <NavLink
                  href={process.env.BACKEND_URL + "/"}
                  className="nav-link"
                >
                  Home
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href={process.env.BACKEND_URL + "/about"}>
                  About
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href={process.env.BACKEND_URL + "/contactInfo"}>
                  Contact
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href={process.env.BACKEND_URL + "/revelerProcess"}>
                  Process
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href={process.env.BACKEND_URL + "/services"}>
                  Services
                </NavLink>
              </NavItem>
            </Nav>
          </div>
        </Collapse>
      </Navbar>
    </RMNavbarDiv>
  );
}

export default RMNavbar;
